import React from 'react'
import Bottom from './bottom'
import '../../index.css'
import { withStyles } from '@material-ui/core/styles'
import 'typeface-roboto'
import { Helmet } from "react-helmet"
import { Link, StaticQuery, graphql } from "gatsby"
import icon from '../../static/favicon.ico'
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Button from '@material-ui/core/Button';
import { camelCase } from 'lodash'

const MyLink = props => <Link {...props} />

const styles = {
  root: {
    position: 'fixed',
    top: 0,
  },
  main: {
    width: '100%',
  },
  // mynavigator: {
  //   top: 0,
  //   width: '100%',
  //   position: 'absolute',
  //   zIndex: 100,
  //   display: 'flex',
  //   justifyContent: 'center',
  // },
  btnGroup: {
  },
}


class Layout extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
    }
  }
  showNavBarBackground() {
    var nav = document.querySelector(".mynavigator")
    nav.classList.toggle("white-bg", window.scrollY > 0)
  }

  go2homePage() {
    window.location.href = "/"
  }

  componentDidMount() {
    window.addEventListener('scroll', this.showNavBarBackground.bind(this));
    this.setState({
    })
  }

  render() {
    // const { open, iOS } = this.state
    const { classes, title, navStyle = {}, wrapStyle = {} } = this.props

    return (
      <StaticQuery
        query={graphql`
          {
              siteConfig {
                  pageSize
                  siteUrl
                  title
                  description
                  copyrightUrl
                  copyrightName
                  commentOpen
                  aboutPostSlug
              }
              sourceConfig(name: {eq: "posts"}) {
                table
              }
          }`}
        render={data => (
          <div className={classes.main}>
            <Helmet defaultTitle={`${data.siteConfig.title}${title ? ` - ${title}` : ""}`}>
              <html lang="zh-cn-Hans" />
              <meta name="description" content={`${data.siteConfig.title} `} />
              <link rel="shortcut icon" href={icon} />
              <noscript>
                为了更好的浏览体验，请不要在本页面禁用 Javascript
                    </noscript>
            </Helmet>
            <div style={{
              height: 36,
              position: "fixed",
              width: '100%',
              top: 0,
              zIndex: 999,
              ...navStyle
            }}>
              <div className='mynavigator'>
                <ButtonGroup className={classes.btnGroup} variant="text" color="primary" aria-label="text primary button group">
                  <Button><MyLink to={'/'}>Home</MyLink></Button>
                  <Button className="btn-group-hidden"><MyLink to={'/categories'}>Categories</MyLink></Button>
                  <Button className="btn-group-hidden"><MyLink to={'/tags'}>Tags</MyLink></Button>
                  <Button><MyLink to={'/archives'}>Archives</MyLink></Button>
                  <Button><MyLink to={`/posts/${data.siteConfig.aboutPostSlug}`}>About</MyLink></Button>
                  <Button><MyLink to={'/links'}>Links</MyLink></Button>
                </ButtonGroup>
              </div>
            </div>
            <div id="top"></div>
            <div style={{ margin: `0 auto`, marginTop: '40px', ...wrapStyle }}>
              {this.props.children}
            </div>
            <Bottom siteConfig={data.siteConfig} />
          </div>
        )}
      />
    )
  }
}

export default withStyles(styles)(Layout)
